import React from 'react'
import Link from 'gatsby-link'
import ArcRule from '../components/ArcRule'

const IndexPage = () => (
  <div>
    <section className="pitch">
      <p>Visual Perspective combines expertise in data science, visualization, GIS, and remote sensing.</p>
      <p>Need help making a map, interactive visualization, or just making sense of your data?</p>
    </section>
    <section className="contact-us">
      <ArcRule />
      <button>Contact Us</button>
    </section>
  </div>
)

export default IndexPage



